import { Component, ViewEncapsulation } from '@angular/core';
import { LoginService } from './login.service'
import { User } from './user.class'

@Component({
  selector: 'login',
  styles: [ require('./login.style.scss') ],
  template: require('./login.template.html'),
  providers: [ LoginService ],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'login-page app'
  }
})
export class Login {
  userLogin(name,pass){
    let u:User = new User(name,pass);
    this.loginService.doLogin(u).then(r=>localStorage.setItem('leyline_token',r.access_token));
  }

  userReg(name,pass){
    let u:User = new User(name,pass);
    this.loginService.doReg(u).then(r=>this.userLogin(name,pass));
  }

  constructor(public loginService:LoginService) {

  }
}



