import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { User } from './user.class';
import { AuthHttp } from 'angular2-jwt';

@Injectable()
export class LoginService {

  private regUrl = 'http://localhost:5000/reg';
  private loginUrl = 'http://localhost:5000/login';

  public doReg(user:any): Promise<any> {
    return this.http.post(this.regUrl,user)
      .toPromise()
      .then(response => response.json() as User)
      .catch(LoginService.handleError);
  }

  public doLogin(user:any): Promise<any> {
    return this.http.post(this.loginUrl,user)
      .toPromise()
      .then(response => response.json() as String)
      .catch(LoginService.handleError);
    /*
     return this.http.get(this.getByGroupUrl+group.toString()+'/page/'+page.toString())
     .toPromise()
     .then(response => response.json() as Site[])
     .catch(GalleryService.handleError);*/
  }


  private static handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  constructor(private http: AuthHttp) { }

}



